.headerapp {
  height: 100vh;
  background-image: linear-gradient(to left, rgba(26, 27, 21, 0.1), #1a1b15), url(/build/img/barberapp.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.headerapp__contenedor {
  position: relative;
  width: min(95%, 144rem);
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.headerapp__barra {
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .headerapp__barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.headerapp__logo {
  flex: 0 0 25%;
}
.headerapp__logo img {
  width: 7rem;
}
.headerapp__nav {
  flex: 0 0 75%;
  display: none;
}
@media (min-width: 768px) {
  .headerapp__nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
}
.headerapp__nav a {
  color: #FFFFFF;
  font-size: 1.5rem;
}
.headerapp__nav a:nth-of-type(4) {
  background-color: rgb(240, 101, 72);
  padding: 2rem 4rem;
}
.headerapp__nav a:nth-of-type(4):hover {
  background-color: transparent;
  border: 1px solid rgb(240, 101, 72);
  color: rgb(240, 101, 72);
}
.headerapp__menu {
  display: block;
}
@media (min-width: 768px) {
  .headerapp__menu {
    display: none;
  }
}
.headerapp__menubars {
  background-color: #585858;
  color: #FFFFFF;
  display: flex;
  justify-content: flex-end;
  padding: 1rem 2rem;
}
.headerapp__bars {
  background-color: #1a1b15;
  padding: 0.7rem 1rem;
  display: flex;
  align-items: center;
}
.headerapp__bars span {
  font-size: 1.6rem;
  font-weight: 600;
  margin-right: 1rem;
}
.headerapp__bars i {
  font-size: 2rem;
}
.headerapp__bars:hover {
  cursor: pointer;
}
.headerapp__navmenu {
  height: 0rem;
  text-align: center;
  background-color: rgba(88, 88, 88, 0.6);
  overflow: hidden;
  transition: all 0.6s ease;
}
.headerapp__navmenu a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
.headerapp__navmenu.mostrar {
  padding: 1rem;
  height: 16rem;
}
.headerapp__contenido {
  height: 100%;
}
@media (min-width: 768px) {
  .headerapp__contenido {
    display: flex;
    align-items: center;
  }
}
.headerapp__texto {
  padding: 2rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .headerapp__texto {
    flex: 0 0 55%;
  }
}
@media (min-width: 992px) {
  .headerapp__texto {
    flex: 0 0 48%;
  }
}
.headerapp__texto h1 {
  font-size: clamp(2rem, -0.8rem + 10vw, 5.5rem);
}
.headerapp__texto p {
  line-height: 1.5;
  margin-bottom: 3rem;
}
.headerapp__texto p span {
  color: #007df4;
}
.headerapp__texto a {
  color: #FFFFFF;
  font-size: 1.5rem;
  background-color: rgb(240, 101, 72);
  padding: 2rem 4rem;
}
.headerapp__texto a:hover {
  background-color: transparent;
  border: 1px solid rgb(240, 101, 72);
  color: rgb(240, 101, 72);
}
.headerapp__login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 30rem;
  width: 100%;
  background-color: white;
}
@media (min-width: 768px) {
  .headerapp__login {
    flex: 0 0 45%;
    position: unset;
    transform: translate(0%, 0%);
    margin: 0 auto;
    background-color: transparent;
  }
}
@media (min-width: 992px) {
  .headerapp__login {
    flex: 0 0 50%;
  }
}

