  }*/
}
.formulario__fieldset {
  border: none;
  padding: 0;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  margin: 0 0 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #64748B;
  width: 4rem;
