  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  border: 1px solid #aeaeae;
  width: 4rem;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: none;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
