  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: center;
}
.formulario__submit--login:hover {
  background-color: rgba(0, 99, 193, 0.4);
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__submit--login {
    background-color: rgba(0, 125, 244, 0.4);
    padding: 1.5rem 4rem;
    color: #FFFFFF;
    font-size: 1.8rem;
    font-weight: 700;
