.inicio {
  width: min(100%, 144rem);
  margin: 0 auto;
}
.inicio .heading {
  color: #585858;
  margin-bottom: 2rem;
}
.inicio .heading i {
  margin-right: 1rem;
  color: #a5a5a5;
}
.inicio .bloques__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 992px) {
  .inicio .bloques__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.inicio .bloques__bloque {
  padding: 1rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  border-bottom: 4px solid #007df4;
  box-shadow: rgba(24, 137, 243, 0.2) 0px 2px 8px 0px;
}
.inicio .bloques__bloque:nth-of-type(2) {
  border-bottom: 4px solid #d2095d;
  box-shadow: rgba(243, 24, 152, 0.2) 0px 2px 8px 0px;
}
.inicio .bloques__bloque:nth-of-type(3) {
  border-bottom: 4px solid rgb(240, 101, 72);
  box-shadow: rgba(240, 101, 72, 0.2) 0px 2px 8px 0px;
}
.inicio .bloques__bloque:nth-of-type(4) {
  border-bottom: 4px solid #00c8c2;
  box-shadow: rgba(24, 236, 243, 0.2) 0px 2px 8px 0px;
}
.inicio .bloques__heading {
  margin: 0;
  margin-bottom: 2rem;
  color: #a5a5a5;
  text-transform: uppercase;
  font-weight: 500;
}
.inicio .bloques__contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.inicio .bloques__contenido p {
  font-size: clamp(1.6rem, -0.8rem + 10vw, 3rem);
  margin: 0;
  color: #585858;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.inicio .bloques__contenido .sign span {
  font-size: 2rem;
  color: #007df4;
}
.inicio .bloques__contenido .plus i {
  font-size: 2rem;
  color: #d2095d;
}
.inicio .bloques__contenido .angule i {
  font-size: 2rem;
  color: rgb(240, 101, 72);
}
.inicio .bloques__contenido .employee i {
  font-size: 2rem;
  color: #00c8c2;
}
.inicio .bloques__contenido .idollar {
  font-size: 4rem;
  color: #007df4;
}
.inicio .bloques__contenido .icalender {
  font-size: 4rem;
  color: #d2095d;
}
.inicio .bloques__contenido .iusers {
  font-size: 4rem;
  color: rgb(240, 101, 72);
}
.inicio .bloques__contenido .icard {
  font-size: 4rem;
  color: #00c8c2;
}

.datos {
  margin-top: 2rem;
}
.datos__contenedorgraficas {
  margin-top: 0rem;
}
@media (min-width: 1024px) {
  .datos__contenedorgraficas {
    display: flex;
    gap: 1rem;
  }
}
.datos__grafica1 {
  background-color: #FFFFFF;
  padding: 1rem;
}
.datos__grafica1 p {
  text-align: center;
  color: #585858;
  font-weight: 400;
}
@media (min-width: 1024px) {
  .datos__grafica1 {
    width: 59%;
  }
}
.datos__titulograficas {
  color: #585858;
}
.datos__completado {
  background-color: #FFFFFF;
}
@media (min-width: 1024px) {
  .datos__completado {
    width: 39%;
  }
}
.datos__bg {
  position: relative;
  height: 10rem;
  background-image: url(/build/img/profile-img.png), linear-gradient(rgba(85, 110, 230, 0.25), rgba(85, 110, 230, 0.25));
  background-repeat: no-repeat;
  background-size: 15rem, cover;
  background-position: center right;
  padding: 2rem;
}
.datos__bg .bg_texto {
  margin: 0;
  color: #007df4;
  font-weight: 500;
  font-size: 1.6rem;
}
.datos__user {
  position: absolute;
  bottom: 0%;
  left: 0%;
  transform: translate(50%, 50%);
  background-color: #FFFFFF;
  border-radius: 50%;
  padding: 1rem;
}
.datos__user i {
  font-size: 3rem;
}
.datos__admininfo {
  margin-top: 2rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.datos__perfil {
  color: #585858;
}
.datos__perfil .nombreperfil {
  margin: 0 0 1rem 0;
  font-weight: 600;
}
.datos__perfil .tipoperfil {
  margin: 0;
}
.datos__infocompletado {
  padding: 0 2rem;
  display: flex;
  justify-content: space-around;
}
.datos__texto .textheading {
  color: #585858;
  font-weight: 700;
  font-size: 1.5rem;
}
.datos__texto .text {
  color: #585858;
  font-size: 1.3rem;
}
.datos__texto .dailyearning {
  color: #585858;
  font-weight: 600;
  margin-bottom: 2rem;
  font-size: clamp(1.5rem, -0.8rem + 10vw, 2.5rem);
}
.datos .rueda {
  position: relative;
  width: 160px;
  /*
  @keyframes anim1{
      100%{stroke-dashoffset: 88;}  //80% html
  }*/
}
.datos .rueda .afuera {
  width: 160px;
  height: 160px;
  /*border: 1px solid #c0e3ee;*/
  box-shadow: 6px 6px 10px -1px rgba(70, 70, 70, 0.15), -1px -1px 12px -3px rgba(207, 207, 207, 0.7);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.datos .rueda .afuera .adentro {
  width: 120px;
  height: 120px;
  /*border: 1px solid #ad3434;*/
  box-shadow: inset 4px 4px 6px -1px rgba(70, 70, 70, 0.2);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.datos .rueda .afuera .adentro .numero {
  color: #64748B;
  font-size: 1.6rem;
}
.datos .rueda circle {
  fill: none;
  stroke: url(#GradientColor1);
  stroke-width: 14px;
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
  transition: all 3s ease;
  background-color: aqua;
}
.datos .rueda svg {
  position: absolute;
  left: 0;
  top: 0;
}
.datos .rueda .name {
  font-size: 1.8rem;
  margin-top: 1rem;
  color: #FFFFFF;
}
.datos__descripcion {
  color: #64748B;
  padding-left: 2rem;
}

.facturacion {
