.dash-cliente {
  background-color: #FFFFFF;
}
.dash-cliente__negocio {
  padding: 1rem;
}
.dash-cliente__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  color: #64748B;
  margin-bottom: 2rem;
  text-align: center;
}
.dash-cliente__rrss .menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.dash-cliente__rrss .menu-redes__enlace::before {
  color: #007df4;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 brands";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://facebook.com"]::before {
  content: "\f39e";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://github.com"]::before {
  content: "\f09b";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://tiktok.com"]::before {
  content: "\e07b";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://instagram.com"]::before {
  content: "\f16d";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://twitter.com"]::before {
  content: "\f099";
}
.dash-cliente__rrss .menu-redes__enlace[href*="https://youtube.com"]::before {
  content: "\f167";
}
.dash-cliente__rrss .menu-redes__ocultar {
  display: none;
}
.dash-cliente .allright {
  color: #64748B;
  font-weight: 300;
}
.dash-cliente .allright span {
  color: #007df4;
  font-weight: 600;
}